@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&family=Roboto+Slab:wght@200;300;700&display=swap');

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  display: flex;
  background-color: white;
  height: 100vh;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none;
  color: inherit;
}
